  height: 100%;
  object-fit: contain;
}
.card__rating {
  font-size: 18px;
  margin-bottom: 32px;
}
.card__rating span {
  background-image: url("../img/star-blue.svg");
}
.card__title {
  font: 400 22px "Prata", sans-serif;
  margin: 0 0 16px;
}
.card__desc {
  margin: 0 0 16px;
}
.card__price {
  margin-top: 32px;
  font: 400 22px "Prata", sans-serif;
}

.intro {
  margin-bottom: 120px;
}
.intro__container {
  background: url("../img/bg/bg-intro.png") no-repeat right center;
  background-size: auto 100%;
  background-origin: content-box;
}
.intro__title {
  font-size: 24px;
  font-size: clamp(24px, 5vw, 55px);
  margin: 0 0 0.75em 0;
}
.intro__footer {
  display: flex;
  align-items: center;
}
.intro__footer-btn {
  margin-right: 2.25rem;
}

.swiper-slide {
  position: relative;
}
.swiper-slide__img-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper-button-next,
.swiper-button-prev {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 5;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.5);
}

.swiper-button-next {
  right: 0;
}

.swiper-pagination {
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 1rem;
  left: 0;
  width: 100%;
  z-index: 2;
}

.swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  margin: 3px;
  border-radius: 50%;
  background-color: #345dee;
}
.swiper-pagination-bullet.swiper-pagination-bullet-active {
  background-color: #8ba434;
}